import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { containerStyles } from './containerStyle';
import { ContainerProperties, ContainerSlot } from './containerProperties';
import { version } from './metadata';
import dragVertical from '@umetrics/sartorius-ui-icon/dist/icons/dragVertical';

@defineElement({
    tagName: 'container',
    styles: containerStyles,
    version,
    //    sub: [Scrollbar]
})
export class Container extends BaseElement implements ContainerProperties {
    // Static members
    public static readonly BUTTON_SLOT = ContainerSlot.BUTTON_SLOT; /* Deprecated, use ContainerSlot */
    public static readonly TITLE_SLOT = ContainerSlot.TITLE_SLOT; /* Deprecated, use ContainerSlot */

    private static Scrollbar;
    private static Icon;
    private static Loader;
    private static SkeletonRectangle;

    // Private members

    // Protected members

    // Public members
    /* deprecated, use heading */
    @property({ reflect: false })
    get title() {
        return this.heading;
    }
    set title(val) {
        const prev = this.heading;
        if (prev !== val) {
            this.heading = val;
            this.requestUpdate('heading', prev);
        }
    }
    @property({ reflect: true })
    heading = '';
    @property({ type: Boolean, attribute: 'auto-size' })
    autoSize = false;
    @property({ type: Boolean, attribute: 'content-padding', converter: booleanConverter })
    contentPadding = true;
    @property({ type: Boolean, attribute: 'content-scroll', converter: booleanConverter })
    contentScroll = false;
    @property({ type: Boolean, attribute: 'center-heading' })
    centerHeading = false;
    @property({ type: Boolean, attribute: 'no-blend-button', converter: booleanConverter })
    noBlendButton = false; /* Deprecated, unused */
    @property({ type: Boolean, attribute: 'no-header', converter: booleanConverter })
    noHeader = false;
    @property({ type: Boolean, attribute: 'no-footer', converter: booleanConverter })
    noFooter = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'new-design' })
    newDesign = false;
    @property({ type: Boolean, converter: booleanConverter })
    draggable = false;
    @property({ type: Boolean, converter: booleanConverter })
    selected = false;
    @property({ type: Boolean, converter: booleanConverter })
    hovered = false;
    @property({ type: Boolean, converter: booleanConverter })
    clickable = false;
    @property({ type: Boolean, converter: booleanConverter, reflect: true })
    fullHeight = false;
    @property({ type: Boolean, converter: booleanConverter })
    get loaded() {
        return this._loaded;
    }

    set loaded(val) {
        if (this._loaded !== val) {
            let timeout = 0;
            if (val === false) {
                timeout = 400;
            }
            setTimeout(() => {
                this._loaded = val;
                this.requestUpdate();
            }, timeout);
        }
    }

    @query('[class$="-container-header"]')
    private _header;
    @query('[class$="-scrollbar"]')
    private _scrollView;
    @query('[class$="-footer"]')
    private _footer;

    @state()
    private _footerSlotted = false;
    @state()
    private _headerSlotted = false;

    private _loaded = true;

    private _mutationObserver: MutationObserver | null = null;
    constructor() {
        super();
        this._handleScroll = this._handleScroll.bind(this);
        this._handleHeaderHover = this._handleHeaderHover.bind(this);
        this._handleHeaderLeave = this._handleHeaderLeave.bind(this);
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-scrollbar'), elem: ['Scrollbar'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-loader'), elem: ['Loader'] });
        imports.push({ module: () => import('@umetrics/sartorius-ui-skeleton'), elem: ['SkeletonRectangle'] });

        Container.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
            this.addEventListener('scroll', this._handleScroll);
        });
    }

    disconnectedCallback() {
        this.removeEventListener('scroll', this._handleScroll);
        if (this.draggable) {
            this._uninstallDraggableHandlers();
        }
        super.disconnectedCallback();
    }

    protected firstUpdated(_changes) {
        super.firstUpdated(_changes);
        this.dispatchEvent(
            new CustomEvent('container-rendered', {
                detail: {
                    item: this,
                },
                bubbles: true,
                composed: true,
            })
        );
        requestAnimationFrame(() => {
            const content = this.shadowRoot!.querySelector('[class$="-content"]');
            if (this._scrollView && content && !this.noFooter && content.clientHeight > this._scrollView.clientHeight) {
                this._footer.style.borderTop = '1px solid #dadada';
            }
        });
        if (this.draggable) {
            this._installDraggableHandlers();
        }
    }

    protected updated(_changes) {
        super.updated(_changes);
        if (_changes.has('draggable')) {
            const prev = _changes.get('draggable');
            if (this.draggable && prev === false) {
                this._installDraggableHandlers();
            } else if (prev === true && !this.draggable) {
                this._uninstallDraggableHandlers();
            }
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            'auto-size': this.autoSize,
            'content-padding': this.contentPadding,
            'content-scroll': this.contentScroll,
            'center-heading': this.centerHeading,
            selected: this.selected,
            hovered: this.hovered,
            draggable: this.draggable,
            clickable: this.clickable,
            'no-footer': this.noFooter || !this._footerSlotted,
            'no-header': this.noHeader || !(this._headerSlotted || this.heading), // Strange, since header will render even though nothing slotted/propped!?
            'new-design': this.newDesign,
            'full-height': this.fullHeight,
        });

        const innerContent = html`
            <div class="${baseClassName}-content">
                <slot></slot>
            </div>
        `;
        const content = this.contentScroll
            ? html`
            <${Container.Scrollbar.tag} class="${baseClassName}-scrollbar" data-testid='container-scrollbar'>
                ${innerContent}
            </${Container.Scrollbar.tag}>        
        `
            : innerContent;

        return html`
            <div class="${rootClasses}">
                <${Container.Loader.tag} toggleable loaded="${this.loaded}">
                    <${Container.SkeletonRectangle.tag} width="100%" height="100%" slot="loader-slot"></${
                        Container.SkeletonRectangle.tag
                    }>
                    ${this._renderHeader(baseClassName)} ${content}
                    <div class="${baseClassName}-footer">
                        <slot name="${ContainerSlot.FOOTER_SLOT}" @slotchange="${(e) =>
                            this._footerSlotChange(e)}"></slot>
                    </div>
                </${Container.Loader.tag}>
            </div>
        `;
    }

    private async _handleScroll() {
        if (this._scrollView && !this.noFooter) {
            // Could be without scrollbar in this, but further down the dom...
            if (this._scrollView.getScrollTop() >= this._scrollView.getScrollHeight() - this._scrollView.clientHeight) {
                this._footer.style.borderTop = '1px solid transparent';
            } else {
                this._footer.style.borderTop = '1px solid #dadada';
            }
        }
    }

    private _footerSlotChange(e) {
        const target = e.target as HTMLSlotElement;
        this._footerSlotted = target.assignedNodes().filter((node) => node.nodeType === 1).length > 0;
    }

    private _headerSlotChange(e) {
        const target = e.target as HTMLSlotElement;
        this._headerSlotted = target.assignedNodes().filter((node) => node.nodeType === 1).length > 0;
    }

    private _renderHeader(baseClassName) {
        const centerHeading = this.centerHeading ? html`<div class="${baseClassName}-spacing"></div>` : null;
        const dragHtml = this.draggable
            ? html`<${Container.Icon.tag} class="${baseClassName}-icon" icon=${dragVertical}></${Container.Icon.tag}>`
            : null;
        const heading = this.heading
            ? html`<h4>${this.heading}</h4>`
            : html`<slot name="${ContainerSlot.TITLE_SLOT}" @slotchange="${(e) => this._headerSlotChange(e)}"></slot>`;

        return this.noHeader
            ? null
            : html`
                  <div
                      class="${baseClassName}-header"
                      @click=${(e) => {
                          if (e.composedPath().find((s) => s.tagName !== 'BUTTON')) {
                              if (!this.draggable && !this.selected && this.clickable) {
                                  this.selected = true;
                                  this._dispatchHeaderClick();
                              }
                          }
                      }}
                  >
                      <slot
                          name="${ContainerSlot.HEADER_SLOT}"
                          @slotchange="${(e) => this._headerSlotChange(e)}"
                      ></slot>
                      ${centerHeading}
                      <div class="${baseClassName}-title-wrapper">${dragHtml} ${heading}</div>
                      <div class="${baseClassName}-buttons">
                          <slot name="${ContainerSlot.BUTTON_SLOT}"></slot>
                      </div>
                  </div>
              `;
    }

    private _installDraggableHandlers = () => {
        const { _header } = this;
        if (_header) {
            if (!this._mutationObserver) {
                this._mutationObserver = this._createMutationObserver();
            }
            _header.addEventListener('mouseover', this._handleHeaderHover);
            _header.addEventListener('mouseleave', this._handleHeaderLeave);
            this._mutationObserver.observe(_header, {
                attributeFilter: ['dropped', 'lifted', 'clicked'],
                attributeOldValue: false,
                subtree: false,
            });
        }
    };

    private _uninstallDraggableHandlers = () => {
        const { _header } = this;
        if (_header) {
            _header.removeEventListener('mouseover', this._handleHeaderHover);
            _header.removeEventListener('mouseleave', this._handleHeaderLeave);
            this._mutationObserver?.disconnect();
            this._mutationObserver = null;
        }
    };

    private _createMutationObserver = () =>
        new MutationObserver((mutationList) => {
            mutationList.forEach((mutation) => {
                const attr = mutation.attributeName;
                if (attr === 'lifted') {
                    this.style.transition = 'box-shadow 0.15s';
                    this.style.boxShadow = '0px 6px 16px 0px rgba(0, 0, 0, 0.20)';
                } else if (attr === 'dropped') {
                    this.style.boxShadow = 'none';
                } else if (attr === 'clicked') {
                    if (!this.selected && this.clickable) {
                        this.selected = true;
                        this._dispatchHeaderClick();
                    }
                }
            });
        });

    private _dispatchHeaderClick() {
        this.dispatchEvent(
            new CustomEvent('header-click', {
                detail: {
                    item: this,
                },
                bubbles: true,
                composed: true,
            })
        );
    }

    private _handleHeaderHover() {
        if (this.draggable) {
            this.hovered = true;
            this.dispatchEvent(
                new CustomEvent('hover-change', {
                    detail: {
                        value: true,
                    },
                    bubbles: true,
                    composed: true,
                })
            );
        }
    }
    private _handleHeaderLeave() {
        this.hovered = false;
        this.dispatchEvent(
            new CustomEvent('hover-change', {
                detail: {
                    value: false,
                },
                bubbles: true,
                composed: true,
            })
        );
    }
}
