export enum ContainerSlot {
    DEFAULT = '',
    TITLE_SLOT = 'title-slot',
    BUTTON_SLOT = 'button-slot',
    FOOTER_SLOT = 'footer-slot',
    HEADER_SLOT = 'header-slot',
}

export interface ContainerProperties {
    heading?: string;
    autoSize?: boolean;
    centerHeading?: boolean;
    contentPadding?: boolean;
    contentScroll?: boolean;
    draggable?: boolean;
    noHeader?: boolean;
    noFooter?: boolean;
    newDesign?: boolean;
    selected?: boolean;
    hovered?: boolean;
    clickable?: boolean;
    fullHeight?: boolean;
    onHeaderClick?: any;
    onHoverChange?: any;
    title?: string /* Deprecated, use prop heading or slot */;
    noBlendButton?: boolean /* Deprecated, unused */;
    loaded?: boolean;
}
