import { css, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const containerStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const white = cssVariableAsVar('color_primary_2');
    const darkerGray = cssVariableAsVar('color_secondary_3');

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
                height: 100%;
                --header-height: 40px;
                --footer-height: 0px;
                background: ${white};
            }

            ${root} {
                display: inline-block;
                width: 100%;
                height: 100%;
                overflow: hidden;
            }

            ${child('title-wrapper')} {
                overflow: hidden;
            }

            ${is('auto-size')} {
                width: auto;
                height: auto;
            }

            ${is('center-heading')} h4 {
                text-align: center;
                flex: 2;
            }

            ${is('center-heading')} ${child('buttons')} {
                flex: 1;
                justify-content: flex-end;
            }

            ${child('spacing')} {
                flex: 1;
            }

            ${is('draggable')} ${child('header')} {
                cursor: pointer;
                padding-left: 12px;
            }

            ${is('draggable')} ${child('title-wrapper')} {
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 16px;
            }

            ${child('icon')} {
                color: ${cssVariableAsVar('color_secondary_4')};
                font-size: 24px;
            }

            ${is('selected')} ${child('header')} {
                box-shadow: 0 4px 0 black inset;
            }

            ${is('selected')} {
                box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.06);
            }

            ${is('hovered')} {
                box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.2);
            }

            ${child('header')} {
                font-family: 'TT Norms Pro';
                width: 100%;
                height: 40px;
                background: ${white};
                display: flex;
                flex-direction: row;
                align-items: center;
                border-bottom: 1px solid ${darkerGray};
                box-sizing: border-box;
                justify-content: space-between;

                padding-left: 24px;
            }

            ${is('clickable', '!draggable')} ${child('header')} {
                cursor: pointer;
            }

            ${is('!clickable', 'draggable')} ${child('header')}:hover {
                cursor: grab;
            }

            ${is('!clickable', 'draggable')} ${child('header')}:active {
                cursor: grabbing;
            }

            ${child('buttons')} {
                display: flex;
                flex-direction: row;
                height: 100%;
                overflow: hidden;
                flex-shrink: 0;
            }

            h4 {
                margin-top: 8px;
                margin-bottom: 8px;
                font-size: 16px;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            ::slotted([slot='title-slot']) {
            }

            ::slotted([slot='header-slot']) {
                padding-right: 24px;
            }

            ${is('auto-size')} ::slotted([slot=title-slot]) {
                width: auto;
                height: auto;
            }

            ${is('content-padding', 'content-scroll')} ${child('scrollbar')} {
                padding: var(--container-inner-spacing, 16px 24px 0px 24px);
            }
            ${is('content-padding', '!content-scroll')} ${child('content')} {
                padding: var(--container-inner-spacing, 16px 24px 0px 24px);
            }
            ${is('content-padding', 'content-scroll', '!no-header')} ${child('scrollbar')} {
                padding: var(--container-inner-spacing, 0px 24px 0px 24px);
            }
            ${is('content-padding', 'content-scroll', 'no-footer')} ${child('scrollbar')} {
                padding: var(--container-inner-spacing, 0px 24px 16px 24px);
            }
            ${is('content-padding', 'content-scroll', 'no-footer', 'no-header')} ${child('scrollbar')} {
                padding: var(--container-inner-spacing, 16px 24px);
            }

            ${is('no-header')} {
                --header-height: 0px;
            }

            ${is('!no-footer')} {
                --footer-height: 72px;
            }

            ${is('!content-scroll')} ${child('content')} {
                height: calc(100% - (var(--header-height) + var(--footer-height)));
            }
            ${is('full-height')} ${child('content')} {
                height: 100%;
            }
            ${is('content-scroll')} ${child('scrollbar')} {
                height: calc(100% - (var(--header-height) + var(--footer-height)));
            }

            ${child('footer')} {
                display: flex;
                height: var(--footer-height);
                border-top: 1px solid transparent;
                justify-content: end;
                align-items: center;
                gap: 16px;
                padding: 16px 24px;
                overflow: hidden;
            }

            ${is('no-footer')} ${child('footer')} {
                display: none;
            }
        `,
    ];
};
